      <!-- 数据统计卡片 -->
      <div class="stats-cards">
        <div class="stat-card">
          <div class="stat-icon field-icon"><i class="fa fa-map"></i></div>
          <div class="stat-content">
            <div class="stat-number">{{ fields.length }}</div>
            <div class="stat-label">总农田数</div>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon area-icon"><i class="fa fa-area-chart"></i></div>
          <div class="stat-content">
            <div class="stat-number">{{ totalArea.toFixed(2) }}亩</div>
            <div class="stat-label">总面积</div>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon active-icon"><i class="fa fa-leaf"></i></div>
          <div class="stat-content">
