            <div class="stat-label">种植中</div>
          </div>
        </div>
        <div class="stat-card">
          <div class="stat-icon alert-icon"><i class="fa fa-exclamation-triangle"></i></div>
          <div class="stat-content">
            <div class="stat-number">{{ alertFields }}</div>
            <div class="stat-label">需要关注</div>
          </div>
        </div>
      </div>
      
      <!-- 农田列表 -->
      <div class="field-list-section">
        <h2>我的农田</h2>
        <div class="field-table-container">
          <table class="field-table">
            <thead>
              <tr>
                <th>农田名称</th>
                <th>位置</th>
                <th>面积</th>
                <th>作物类型</th>
