                    <button class="btn-delete" @click="deleteField(field.id)">
                      <i class="fa fa-trash"></i>
                    </button>
                    <button class="btn-task" @click="createTaskForField(field)">
                      <i class="fa fa-calendar"></i>
                    </button>
