                </td>
              </tr>
            </tbody>
          </table>
          <div v-if="filteredFields.length === 0" class="empty-state">
            <i class="fa fa-map-o"></i>
            <p>没有找到符合条件的农田</p>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 添加/编辑农田弹窗 -->
    <div class="modal" v-if="showAddFieldModal || showEditFieldModal">
      <div class="modal-content">
        <div class="modal-header">
          <h3>{{ showAddFieldModal ? '添加新农田' : '编辑农田信息' }}</h3>
          <button class="close-btn" @click="closeModal">&times;</button>
        </div>
        <div class="modal-body">
          <form @submit.prevent="saveField">
            <div class="form-group">
              <label>农田名称 *</label>
              <input type="text" v-model="currentField.name" required placeholder="请输入农田名称">
            </div>
            <div class="form-row">
              <div class="form-group">
                <label>位置 *</label>
                <input type="text" v-model="currentField.location" required placeholder="请输入农田位置">
              </div>
              <div class="form-group">
                <label>面积(亩) *</label>
                <input type="number" v-model.number="currentField.area" required min="0.1" step="0.1">
              </div>
            </div>
            <div class="form-row">
              <div class="form-group">
                <label>作物类型 *</label>
                <select v-model="currentField.cropType" required>
                  <option value="">请选择作物类型</option>
                  <option value="水稻">水稻</option>
                  <option value="小麦">小麦</option>
                  <option value="玉米">玉米</option>
                  <option value="大豆">大豆</option>
                  <option value="棉花">棉花</option>
                  <option value="其他">其他</option>
                </select>
              </div>
              <div class="form-group">
                <label>土壤类型</label>
                <select v-model="currentField.soilType">
                  <option value="">请选择土壤类型</option>
                  <option value="壤土">壤土</option>
                  <option value="砂土">砂土</option>
                  <option value="粘土">粘土</option>
                  <option value="水稻土">水稻土</option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label>当前状态 *</label>
              <select v-model="currentField.status" required>
                <option value="种植中">种植中</option>
                <option value="休耕">休耕</option>
                <option value="收获中">收获中</option>
                <option value="已收获">已收获</option>
              </select>
            </div>
            <div class="form-group">
              <label>备注信息</label>
              <textarea v-model="currentField.description" rows="3" placeholder="请输入备注信息"></textarea>
            </div>
            <div class="form-actions">
              <button type="button" class="btn-cancel" @click="closeModal">取消</button>
              <button type="submit" class="btn-save">保存</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    
    <!-- 农田详情弹窗 -->
    <div class="modal" v-if="showFieldDetails">
      <div class="modal-content large">
        <div class="modal-header">
          <h3>{{ selectedField.name }} - 详细信息</h3>
          <button class="close-btn" @click="showFieldDetails = false">&times;</button>
        </div>
        <div class="modal-body">
          <div class="field-details-content">
            <div class="detail-section">
              <h4>基本信息</h4>
              <div class="detail-grid">
                <div class="detail-item">
                  <label>农田名称</label>
                  <span>{{ selectedField.name }}</span>
                </div>
                <div class="detail-item">
                  <label>位置</label>
                  <span>{{ selectedField.location }}</span>
                </div>
                <div class="detail-item">
                  <label>面积</label>
                  <span>{{ selectedField.area }}亩</span>
                </div>
                <div class="detail-item">
                  <label>作物类型</label>
                  <span>{{ selectedField.cropType }}</span>
                </div>
                <div class="detail-item">
                  <label>土壤类型</label>
                  <span>{{ selectedField.soilType }}</span>
                </div>
                <div class="detail-item">
                  <label>当前状态</label>
                  <span class="status-badge" :class="getStatusClass(selectedField.status)">{{ selectedField.status }}</span>
                </div>
                <div class="detail-item">
                  <label>创建时间</label>
                  <span>{{ selectedField.createdAt }}</span>
                </div>
                <div class="detail-item">
                  <label>最近更新</label>
                  <span>{{ selectedField.updatedAt }}</span>
                </div>
              </div>
              <div class="detail-item full-width">
                <label>备注信息</label>
                <p>{{ selectedField.description || '无' }}</p>
              </div>
            </div>
            
            <div class="detail-section">
              <h4>种植历史</h4>
              <div class="planting-history">
                <div v-for="history in selectedField.plantingHistory" :key="history.id" class="history-item">
                  <div class="history-period">{{ history.period }}</div>
                  <div class="history-info">
                    <div class="history-crop">{{ history.crop }}</div>
                    <div class="history-details">{{ history.details }}</div>
                  </div>
                </div>
                <div v-if="selectedField.plantingHistory.length === 0" class="empty-history">
                  暂无种植历史记录
                </div>
              </div>
            </div>
            
            <div class="detail-section">
              <h4>最近作业记录</h4>
              <div class="recent-operations">
                <div v-for="operation in selectedField.recentOperations" :key="operation.id" class="operation-item">
                  <div class="operation-type">{{ operation.type }}</div>
                  <div class="operation-date">{{ operation.date }}</div>
                  <div class="operation-status" :class="operation.status === '完成' ? 'success' : 'warning'">{{ operation.status }}</div>
                </div>
                <div v-if="selectedField.recentOperations.length === 0" class="empty-operations">
                  暂无作业记录
                </div>
              </div>
            </div>
            
            <div class="detail-section">
              <h4>农田状态</h4>
              <div class="status-indicators">
                <div class="status-item">
                  <div class="status-label">健康指数</div>
                  <div class="status-value">{{ selectedField.healthIndex }}%</div>
                  <div class="progress-bar">
                    <div class="progress-fill" :style="{ width: selectedField.healthIndex + '%', backgroundColor: getHealthColor(selectedField.healthIndex) }"></div>
                  </div>
                </div>
                <div class="status-item">
                  <div class="status-label">土壤湿度</div>
                  <div class="status-value">{{ selectedField.soilMoisture }}%</div>
                  <div class="progress-bar">
                    <div class="progress-fill" :style="{ width: selectedField.soilMoisture + '%', backgroundColor: getMoistureColor(selectedField.soilMoisture) }"></div>
                  </div>
                </div>
                <div class="status-item">
                  <div class="status-label">养分水平</div>
                  <div class="status-value">{{ selectedField.nutrientLevel }}%</div>
                  <div class="progress-bar">
                    <div class="progress-fill" :style="{ width: selectedField.nutrientLevel + '%', backgroundColor: getNutrientColor(selectedField.nutrientLevel) }"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FarmFieldManagement',
  data() {
    return {
      fields: [],
      searchQuery: '',
      cropFilter: '',
      statusFilter: '',
      showAddFieldModal: false,
      showEditFieldModal: false,
      showFieldDetails: false,
      currentField: {
        id: null,
        name: '',
        location: '',
        area: 0,
        cropType: '',
        soilType: '',
        status: '种植中',
        description: '',
        alert: false,
        lastOperation: '',
        createdAt: '',
        updatedAt: '',
        healthIndex: 0,
        soilMoisture: 0,
        nutrientLevel: 0,
        plantingHistory: [],
        recentOperations: []
      },
      selectedField: {}
    }
  },
  computed: {
    filteredFields() {
      return this.fields.filter(field => {
        const matchesSearch = field.name.toLowerCase().includes(this.searchQuery.toLowerCase()) || 
                            field.location.toLowerCase().includes(this.searchQuery.toLowerCase());
        const matchesCrop = !this.cropFilter || field.cropType === this.cropFilter;
        const matchesStatus = !this.statusFilter || field.status === this.statusFilter;
        return matchesSearch && matchesCrop && matchesStatus;
      });
    },
    totalArea() {
      return this.fields.reduce((total, field) => total + field.area, 0);
    },
    activeFields() {
      return this.fields.filter(field => field.status === '种植中').length;
    },
    alertFields() {
      return this.fields.filter(field => field.alert).length;
    }
  },
  mounted() {
    this.loadFields();
  },
  methods: {
    // 加载农田数据
    loadFields() {
      // 模拟从API加载数据
      this.fields = [
        {
          id: 1,
          name: '试验田A区',
          location: '东区农田基地',
          area: 50,
          cropType: '水稻',
          soilType: '水稻土',
          status: '种植中',
          alert: false,
          lastOperation: '2024-01-15',
          createdAt: '2023-12-01',
          updatedAt: '2024-01-10',
          healthIndex: 85,
          soilMoisture: 65,
          nutrientLevel: 78,
          description: '主要试验田，种植优质水稻品种',
          plantingHistory: [
            { id: 101, period: '2023-Q4', crop: '水稻', details: '种植优质杂交水稻品种' },
            { id: 102, period: '2023-Q3', crop: '小麦', details: '冬小麦种植实验' }
          ],
          recentOperations: [
            { id: 201, type: '病虫害防治', date: '2024-01-15', status: '完成' },
            { id: 202, type: '灌溉作业', date: '2024-01-10', status: '完成' },
            { id: 203, type: '施肥作业', date: '2024-01-05', status: '完成' }
          ]
        },
        {
          id: 2,
          name: '试验田B区',
          location: '西区农田基地',
          area: 30,
          cropType: '小麦',
          soilType: '壤土',
          status: '种植中',
          alert: true,
          lastOperation: '2024-01-12',
          createdAt: '2023-12-05',
          updatedAt: '2024-01-12',
          healthIndex: 62,
          soilMoisture: 45,
          nutrientLevel: 60,
          description: '小麦种植试验区，需要关注土壤湿度',
          plantingHistory: [
            { id: 103, period: '2023-Q4', crop: '小麦', details: '冬小麦新品种试验' }
          ],
          recentOperations: [
            { id: 204, type: '灌溉作业', date: '2024-01-12', status: '完成' },
            { id: 205, type: '监测作业', date: '2024-01-10', status: '完成' }
          ]
        },
        {
          id: 3,
          name: '试验田C区',
          location: '南区农田基地',
          area: 45,
          cropType: '玉米',
          soilType: '砂土',
          status: '休耕',
          alert: false,
          lastOperation: '2023-12-20',
          createdAt: '2023-11-20',
          updatedAt: '2023-12-20',
          healthIndex: 75,
          soilMoisture: 55,
          nutrientLevel: 70,
          description: '玉米试验田，当前休耕期',
          plantingHistory: [
            { id: 104, period: '2023-Q3', crop: '玉米', details: '夏玉米高产试验' }
          ],
          recentOperations: [
            { id: 206, type: '土壤改良', date: '2023-12-20', status: '完成' }
          ]
        },
        {
          id: 4,
          name: '示范田D区',
          location: '北区农田基地',
          area: 60,
          cropType: '大豆',
          soilType: '粘土',
          status: '收获中',
          alert: false,
          lastOperation: '2024-01-18',
          createdAt: '2023-11-01',
          updatedAt: '2024-01-18',
          healthIndex: 90,
          soilMoisture: 60,
          nutrientLevel: 85,
          description: '大豆示范田，高产优质',
          plantingHistory: [
            { id: 105, period: '2023-Q4', crop: '大豆', details: '高蛋白大豆品种示范' }
          ],
          recentOperations: [
            { id: 207, type: '收获作业', date: '2024-01-18', status: '进行中' }
          ]
        }
      ];
    },
    
    // 打开添加农田弹窗
    openAddFieldModal() {
      this.resetCurrentField();
      this.showAddFieldModal = true;
    },
    
    // 编辑农田
    editField(field) {
      this.currentField = { ...field };
      this.showEditFieldModal = true;
    },
    
    // 查看农田详情
    viewFieldDetails(field) {
      this.selectedField = { ...field };
      this.showFieldDetails = true;
    },
    
    // 删除农田
    deleteField(fieldId) {
      if (confirm('确定要删除这个农田吗？')) {
        const index = this.fields.findIndex(field => field.id === fieldId);
        if (index > -1) {
          this.fields.splice(index, 1);
          alert('农田删除成功');
        }
      }
    },
    
    // 保存农田信息
    saveField() {
      const now = new Date().toISOString().split('T')[0];
      
      if (this.showAddFieldModal) {
        // 新增农田
        const newField = {
          ...this.currentField,
          id: Date.now(),
          createdAt: now,
          updatedAt: now,
          alert: false,
          lastOperation: '',
          healthIndex: 80,
          soilMoisture: 60,
          nutrientLevel: 70,
          plantingHistory: [],
          recentOperations: []
        };
        this.fields.push(newField);
        alert('农田添加成功');
      } else if (this.showEditFieldModal) {
        // 编辑农田
        const index = this.fields.findIndex(field => field.id === this.currentField.id);
        if (index > -1) {
          this.fields[index] = {
            ...this.fields[index],
            ...this.currentField,
            updatedAt: now
          };
          alert('农田信息更新成功');
        }
      }
      
      this.closeModal();
    },
    
    // 关闭弹窗
    closeModal() {
      this.showAddFieldModal = false;
      this.showEditFieldModal = false;
      this.resetCurrentField();
    },
    
    // 重置当前农田对象
    resetCurrentField() {
      this.currentField = {
        id: null,
        name: '',
        location: '',
        area: 0,
        cropType: '',
        soilType: '',
        status: '种植中',
        description: ''
      };
    },
    
    // 为农田创建任务
    createTaskForField(field) {
      // 这里可以跳转到任务创建页面，并预填充农田信息
      alert(`为${field.name}创建作业任务`);
      // 实际项目中可以使用路由跳转：this.$router.push({ path: '/task/create', query: { fieldId: field.id } });
    },
    
    // 获取作物类型样式类
    getCropClass(cropType) {
      const classMap = {
        '水稻': 'crop-rice',
        '小麦': 'crop-wheat',
        '玉米': 'crop-corn',
        '大豆': 'crop-soybean',
        '棉花': 'crop-cotton'
      };
      return classMap[cropType] || 'crop-other';
    },
    
    // 获取状态样式类
    getStatusClass(status) {
      const classMap = {
        '种植中': 'status-active',
        '休耕': 'status-idle',
        '收获中': 'status-harvesting',
        '已收获': 'status-completed'
      };
      return classMap[status] || '';
    },
    
    // 获取健康指数颜色
    getHealthColor(index) {
      if (index >= 80) return '#28a745';
      if (index >= 60) return '#ffc107';
      return '#dc3545';
    },
    
    // 获取湿度颜色
    getMoistureColor(moisture) {
      if (moisture >= 60 && moisture <= 80) return '#28a745';
      if (moisture >= 40 || moisture <= 90) return '#ffc107';
      return '#dc3545';
    },
    
    // 获取养分颜色
    getNutrientColor(nutrient) {
      if (nutrient >= 70) return '#28a745';
      if (nutrient >= 50) return '#ffc107';
      return '#dc3545';
    }
  }
}
</script>

